.TrendCard {
    background-color: white;
    border-radius: 1rem;
    z-index: 3;
    padding: 1.5rem;
    display: flex;
    flex-direction: column;
    gap: 1rem;box-shadow: rgba(0, 0, 0, 0.09) 0px 3px 12px 0px;
}
h3 {
    font-weight: 500;
    font-size: 1rem;
}
p {
    font-weight: 100;
    font-size: .8rem;

}
.Trend {
    display: flex;
    justify-content: space-between;

}
.Trend img {
    width: 15px;
    margin-right: .3rem;
}