.HomeMessage{
    display: flex;
    flex-direction: column;
    width: 100%;
    background: white;
    border-radius: 1rem;
    padding: 1rem;
    gap: 1rem;box-shadow: rgba(0, 0, 0, 0.09) 0px 3px 12px 0px;
}
.HomeMessage img {
    width: 50px;
}
.title {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}
.title img {
    width: 20px;
}
.searchMessage {
    display: flex;
    flex-direction: row;
    padding:.3rem 1rem;
    border-radius: 1rem;
    background: var(--gray);
    align-items: center;
}
.searchMessage input {
    background: var(--gray);
}
.searchMessage img {
    width: 20px;
    height: 20px;
}
.messageTabs {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    justify-content: space-between;
 
}
.tab_ {
    border-bottom: 5px solid rgba(53, 176, 247, 0.404);

    padding: 1rem 0;
    text-align: center;
    cursor: pointer;
}
.tab_:hover{
    border-bottom: 5px solid var(--orange);

}
.tab_:first-child{
    border-bottom: 5px solid var(--orange);

}
.allUsersInfo {
    display: flex;
    flex-direction: column;
    gap: 1rem;
}
.userInfo img{
   width: 50px;
   height: 50px;
   filter: drop-shadow(0 1mm 1.2mm  rgba(0, 0, 0, 0.263));
}.userInfo h1 {
    opacity: .9;
    font-weight: 500;
}
.userInfo {
    display: flex;
    gap: .5rem;

}
