.LogoSearch {
    display: grid;
    flex-direction: row;
    align-items: center;
    grid-template-columns: 1fr 4fr;
    /* gap: 0.75rem; */ 
    background: white;
    padding:.2rem .5rem;
    width: 100%;
    border-radius: 1rem;
    box-shadow: rgba(0, 0, 0, 0.09) 0px 3px 12px 0px;

}

.LogoInput{
    background-color: var(--inputColor);
    /* con dc huong file import cua cha */
    padding: .3rem;
    width: 100%;
    display: flex;
    
    border-radius: .4rem;
    

}
.LogoInput>input {
    outline: none;
    border: none;
    width: 100%;
    background: transparent;

}
.LogoInput svg{
   
    padding: .2rem;
    border-radius: .4rem;
    color: white;
    background: linear-gradient(var(--orange), rgb(109, 175, 247));
}
.LogoSearch a  >img{
    width: 50px;
}